a.scrolltotop {
  background: #d0963e none repeat scroll 0 0;
  border: 1px solid transparent;
  bottom: 20px;
  color: #fff;
  display: none;
  font-size: 20px;
  height: 50px;
  padding-top: 10px;
  position: fixed;
  right: 20px;
  text-align: center;
  width: 50px;
  z-index: 99; }

a.scrolltotop:hover {
  background: #1a1a1a;
  border: 1px solid; }
